<template>
  <el-row>
    <el-col :span="24">
      <el-row type="flex" class="row-bg" justify="space-between">
        <el-col :span="24">
          <el-row type="flex" class="row-bg" justify="space-between">
            <el-col :span="12">
              <h3>流量转发配置</h3>
            </el-col>
      
          </el-row>
        </el-col>
      </el-row>
      <el-divider />
      <el-row class="text-align-right">
        <div class="domain-search-input">
          <el-input placeholder="请输入名称进行搜索" prefix-icon="el-icon-search" v-model="domainSearch"></el-input>
        </div>
        <el-button type="primary" @click="onClickCreateIdentity()">新增</el-button>
      </el-row>
      <div class="demo-block">
        <el-table :data="
          tableData.filter(
            (data) =>
              !domainSearch ||
              data.name.toLowerCase().includes(domainSearch.toLowerCase())
          )
        " style="width: 100%">
          <el-table-column prop="name" label="名称"></el-table-column>
          <el-table-column prop="detail" label="详情"></el-table-column>
          <el-table-column prop="traffic_forward_ip" label="转发地址"></el-table-column>
          <el-table-column prop="traffic_forward_port" label="转发端口"></el-table-column>
          
          <el-table-column label="操作" align="right">
            <template #default="scope">
              <el-button size="mini" @click="handleEdit(scope.row)" class="button-block" type="text"
                :loading="scope.row.loading">编辑</el-button>
              
              <el-popover placement="top" width="160" v-model:visible="scope.row.isVisiblePopover">
                <p>确定删除吗？</p>
                <div style="text-align: right; margin: 0">
                  <el-button size="mini" type="text" @click="scope.row.isVisiblePopover = false">取消</el-button>
                  <el-button type="primary" size="mini" @click="handleDelete(scope.row)" :loading="loading">确定
                  </el-button>
                </div>
                <template #reference>
                  <el-button type="text" size="mini" @click="scope.row.isVisiblePopover = true">删除</el-button>
                </template>

              </el-popover>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-col>

    <el-dialog :title="domainTitle" v-model="dialogIdentityFormVisible" width="820px" :close-on-click-modal="false"
      @closed="dialogClose">
      <el-form class="form-tag-dialog identity-edit-form " :model="identityForm" size="mini" label-position="left" label-width="130px"
        :rules="rules" ref="identityForm">
        <el-form-item label="名称" prop="name" key="1">
          <el-input v-model="identityForm.name" v-if="domainType == 'new'" placeholder="请输入字母开头，只包含字母、数字、下划线“_”、中横线“-”">
          </el-input>
          <el-input v-model="identityForm.name" v-if="domainType == 'edit'" disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="详情" key="2">
          <el-input v-model="identityForm.detail" placeholder="请输入详情"></el-input>
        </el-form-item>

        <el-form-item label="自定义header头" key="3">
          <el-switch v-model="identityForm.set_request_header_status" active-value="true" inactive-value="false"></el-switch>
        </el-form-item>
        
        <div v-if="identityForm.set_request_header_status == 'true'">
          <el-form-item label="响应头配置" class="longer-cascader traffic-forward-item">
            <div class="match-box" v-for="(item, index) in ruleConfigMatchs" :key="'matchs' + index">
              <div class="match_key_title">
                <el-input v-model="item.key" placeholder="key"> </el-input>
              </div>
              <div class="match-box-type">
                <el-select v-model="item.type" placeholder="请选择">
                  <el-option label="设置值" value="set_value" />
                  <el-option label="删除值" value="del_value" />
                </el-select>
              </div>
              <div class="match-box-content">
                <el-input v-model="item.value" placeholder="value"  v-show="item.type == 'set_value'"> </el-input>
              </div>
        
              <el-button @click.prevent="removeConfigMatchs(item, bigIndex)">删除</el-button>
            </div>
            <el-button @click="addConfigMatchs(bigIndex)" plain type="primary" class="button-new">新增</el-button>
          </el-form-item>
        </div>

        <el-form-item label="转发地址" key="4">
          <el-tag :key="index" v-for="(tag, index) in sourceIpList" closable :disable-transitions="false"
            @close="handleCloseSourceIpList(tag)">{{ tag }}</el-tag>
          <el-input class="input-new-tag node-ip-list" v-if="sourceIpListVisible" v-model="sourceIpListValue"
            ref="saveTagSourceIpList" size="mini" @keyup.enter="handleSourceIpListConfirm" @blur="handleSourceIpListConfirm">
          </el-input>
          <el-button v-else class="button-new-tag" size="small" @click="showSourceIpList">
            <i class="el-icon-plus"></i>
          </el-button>
        </el-form-item>
        
        <el-form-item label="转发端口" prop="traffic_forward_port" key="5">
          <el-input v-model="identityForm.traffic_forward_port"></el-input>
        </el-form-item>
      

      </el-form>
      <template #footer class="dialog-footer">
        <el-button @click="dialogIdentityFormVisible = false">取消</el-button>
        <el-button type="primary" @click="onClickIdentitySubmit('identityForm')" :loading="loading">确定</el-button>
      </template>
    </el-dialog>
  </el-row>
</template>
<script>
import { validatePort, validateRuleName, mixin, JXAjax } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      domainTitle: "新增",
      domainType: "new",
      domainSearch: "",
      dialogIdentityFormVisible: false,
      loading: false,
      identityForm: {
        name: "",
        detail: "",
        traffic_forward_ip: "",
        traffic_forward_port: "80",
      },
      tableData: [],
      sourceIpList: [],
      sourceIpListVisible: false,
      sourceIpListValue: "",
      flagTag: true,
      ruleConfigMatchs: [{
        key: "",
        type:"set_value",
        value: "",
      }],
    };
  },
  computed: {
    rules() {
      return {
        name: [
          {
            required: true,
            message: '请输入字母开头，只包含字母、数字、下划线“_”、中横线“-”',
            trigger: ["blur", "change"],
          },
          {
            validator: validateRuleName,
            trigger: ["blur", "change"],
          },
        ],
        traffic_forward_port: [
          {
            required: true,
            message: '请输入转发端口信息',
            trigger: ["blur", "change"],
          },
          {
            validator: validatePort,
            trigger: ["blur", "change"],
          },
        ],
      }
    }
  },
  
  mounted() {
    this.getData();
  },
  methods: {
    handleCloseSourceIpList(tag) {
      this.sourceIpList.splice(this.sourceIpList.indexOf(tag), 1);
    },
    showSourceIpList() {
      this.sourceIpListVisible = true;
      this.$nextTick((_) => {
        this.$refs.saveTagSourceIpList.$refs.input.focus();
      });
    },
    handleSourceIpListConfirm() {
      let t = this;
      let sourceIpListValue = this.sourceIpListValue;
      let pattern = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
      //let pattern = /^(((https|http)?:\/)?\/)/;
      if (t.flagTag) {
        t.flagTag = false;
        if (sourceIpListValue) {
          if (pattern.test(sourceIpListValue)) {
            JXAjax(
              "post",
              "/ip_check",
              { ip: sourceIpListValue },
              function (response) {
                if (response.data.result) {
                  t.sourceIpList.push(sourceIpListValue);
                  t.sourceIpListVisible = false;
                  t.sourceIpListValue = "";
                } else {
                  t.$message({
                    showClose: true,
                    message: "请输入正确的IP地址",
                    type: "error",
                  });
                }
              },
              function () {

              },
              "no-message"
            );

          } else {
            t.$message({
              showClose: true,
              message: "请输入转发IP地址",
              type: "error",
            });
          }
        } else {
          t.sourceIpListVisible = false;
          t.sourceIpListValue = "";
        }
        setTimeout(function () {
          t.flagTag = true;
        }, 50);
      }
    },
    getData() {
      var t = this;
      JXAjax(
        "get",
        "/waf/waf_get_sys_traffic_forward_list",
        {},
        function (response) {
          t.tableData = response.data.message;
          t.tableData.forEach(item => {
            item.isVisiblePopover = false;
          })
        },
        function () {

        }
      );
    },

    dialogClose() {
      this.identityForm = {
        name: "",
        detail: "",
        set_request_header_status: "false",
        traffic_forward_ip: "",
        traffic_forward_port: "80",
      };
      this.ruleConfigMatchs = [{
        key: "",
        value: "",
        type: "set_value",
      }];
      this.sourceIpListVisible = false;
      this.sourceIpListValue = "";
      this.sourceIpList = [];
      this.$refs["identityForm"].resetFields();
    },


    onClickIdentitySubmit(identityForm) {
      var t = this;
      var postUrl = "/waf/waf_create_sys_traffic_forward";
      if (t.domainType == "edit") {
        postUrl = "/waf/waf_edit_sys_traffic_forward";
      }

      if (t.sourceIpList.length == 0) {
        t.$message({
          message: 'IP不能为空',
          type: "error",
        });
        return false;
      }
      if (t.identityForm.set_request_header_status == 'true') {
        if (t.ruleConfigMatchs.length == 0) {
          t.$message({
            message: '请对自定义header头进行配置',
            type: "error",
          });
          return false;
        }
      }
      t.identityForm.set_request_header_value = JSON.stringify(t.ruleConfigMatchs);
      t.identityForm.traffic_forward_ip = t.sourceIpList.join(",");
      this.$refs[identityForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.identityForm,
            function (response) {
              t.loading = false;
              t.dialogIdentityFormVisible = false;
              t.sourceIpListVisible = false;
              t.sourceIpListValue = "";
              t.sourceIpList = [];
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
    
    onClickCreateIdentity() {
      var t = this;
      t.domainTitle = "新增";
      t.domainType = "new";
      t.dialogIdentityFormVisible = true;
    },

    handleEdit(data) {
      var t = this;
      data.loading = true;
      JXAjax(
        "post",
        "/waf/waf_get_sys_traffic_forward",
        { name: data.name },
        function (response) {
          data.loading = false;
          var oGetData = response.data.message;
          t.identityForm.name = oGetData.name;
          t.identityForm.detail = oGetData.detail;
          t.identityForm.traffic_forward_ip = oGetData.traffic_forward_ip;
          t.identityForm.traffic_forward_port = oGetData.traffic_forward_port;
          t.sourceIpList = oGetData.traffic_forward_ip
            ? oGetData.traffic_forward_ip.split(",")
            : [];

          t.identityForm.set_request_header_status = oGetData.set_request_header_status;
          t.identityForm.set_request_header_value = oGetData.set_request_header_value;
          if (t.identityForm.set_request_header_status == "true") {
            var header_value = JSON.parse(t.identityForm.set_request_header_value)
            var box = []
            for (var i in header_value) {
              var item = header_value[i]
              box.push({
                key: item.key,
                value: item.value,
                type: item.type
              })
            }
            t.ruleConfigMatchs = box;
          }
          t.domainTitle = '编辑';
          t.domainType = "edit";
          t.dialogIdentityFormVisible = true;
        },
        function () {
          data.loading = false;
        },
        "no-message"
      );
    },

    handleDelete(data) {
      var t = this;
      t.loading = true;
      JXAjax(
        "post",
        "/waf/waf_del_sys_traffic_forward",
        { name: data.name },
        function (response) {
          data.isVisiblePopover = false;
          t.loading = false;
          t.getData();
        },
        function () {
          t.loading = false;
        }
      );
    },
    removeConfigMatchs(item) {
      var index = this.ruleConfigMatchs.indexOf(item);
      if (index > 0) {
        this.ruleConfigMatchs.splice(index, 1);
      }
    },

    addConfigMatchs() {
      this.ruleConfigMatchs.push({ key: "", value: "", type: "set_value"})
    },
  },
};
</script>
<style>
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}
.icon-success {
  color: #67c23a;
  margin-right: 5px;
}
.icon-error {
  color: #f56c6c;
  margin-right: 5px;
}
.icon-warning {
  color: #e6a23c;
  margin-right: 5px;
}
.domain-setting {
  float: right;
}
.domain-redirect {
  margin-top: 5px;
  margin-bottom: 0px !important;
}

.el-form-item.is-required .redirect-box .el-form-item__label:before {
  content: "";
}
.domain-search-input {
  display: block;
  float: left;
  text-align: left;
}
.domain-search-input .el-input {
  width: 100%;
}
.no-padding {
  padding: 0;
}
.node-detail span {
  font-size: 12px;
}
.domain-tabs {
  margin-bottom: 18px;
}

.identity-edit-form .el-button {
  margin-left: 10px;
}
.identity-edit-form .button-new-tag {
  margin-left: 0px;
}

.identity-edit-form .button-new {
  margin-left: 0px;
}

.identity-edit-form .match-box-content {
  position: relative;
  display: inline-block;
}

.identity-edit-form .match_key_input {
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
  width: 100%;
}

.identity-edit-form .match_key_title {
  width: 100px;
  display: inline-block;
  margin-right: 10px;
}
.identity-edit-form .longer-cascader .match-box {
  width: calc(100% - 74px);
}

.longer-cascader .match_key_cascader {
  width: 100%;
}

.longer-cascader .el-cascader {
  width: 100%;
}


.identity-edit-form .longer-cascader.traffic-forward-item .match-box-content {
  width: calc(100% - 290px);
  margin-left: 10px;
}


.identity-edit-form .match-box {
  display: inline-block;
  margin-bottom: 10px;
  margin-right: 10px;
}

.identity-edit-form .match-box-type{
  position: relative;
  display: inline-block;
  width: 100px;
}
</style>
